import './styles.css';
import ListTemplate from './modules/app.js';
import clearCompleted from './modules/clearAll.js';

const addBtn = document.querySelector('.addToDo');
const todoInput = document.querySelector('.toDoInput');

const clearAll = document.querySelector('.clearCompleted');

const task = new ListTemplate();

window.addEventListener('load', () => {
  task.displayList();
});

addBtn.addEventListener('click', () => {
  task.addList(todoInput.value, false, JSON.parse(localStorage.getItem('listStorage')).length + 1);
  task.displayList();
});

clearAll.addEventListener('click', () => {
  clearCompleted();
  task.displayList();
});
